.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

canvas.canvas{
  background-color: #ffffff;
  width:100%;
  height:100%;
  border: green 1px solid;
}

div.divcansas {
  border: red 1px solid;
  overflow: hidden;
  resize: both;
  height: 400px; 
  width:400px;
  margin:10px;
  padding:10px;
  background-color: olive;
}

div.App{
  background-color: silver;
  height: 90vh;
}

div.container{
  margin-top: 75px;
  color: #3f51b5;
}

footer{
  background-color: #3f51b5;
  height: 10vh;
  width: 100%;
  position: absolute;
  bottom: 0;
  color: #ffffff !important;
}

select.form-control{
  width: 120px;
}

div.options{
  text-align: left;
}

@media screen and (max-width: 400px) {
  div.xsbt {
     /*background-color: olive;*/
     background-color: silver;
     top: 50px;
     position: relative;
   }
   footer{
    height: 10vh;
    top: 50px;
    bottom: 0 !important;
    position: relative !important;
   }
   div.container{
    margin-left: 0 !important;
    padding-left: 0 !important;
   }
   div.divcansas{
     margin-left: 0 !important;
     padding-left: 0 !important;
     background-color: olive;
   }
 }